#index-header {
    display: none;
  }

    #mobile-header {
    position: sticky;
    top: 0;
    z-index: 3;
    width: 100%;
    border-bottom: solid 4px var(--main-color);
    display: block;

}

#mobile-nav {
  background: linear-gradient(
    to right,
    var(--main-color),
    var(--third-color)
  );
  width: 100%;
}

#mobile-nav-bar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.5rem 1rem;
  height: 70px;
  box-sizing: border-box;
}

#mobile-logo {
  height: 100%;
  display: flex;
  align-items: center;

}

#mobile-logo img {
  height: 100%;
  border: solid white 2px;
  transition: 0.1s;
  width: 130px;
}

#mobile-logo img:hover {
  border-color: var(--secondary-color);
}

#mobile-menu-btn {
  font-size: 2rem;
  background: none;
  border: none;
  color: white;
  cursor: pointer;
}

#mobile-menu {
  background-color: var(--third-color);
  height: 100vh;
  width: 99%;
  z-index: 3;
  position: absolute;
  top: 0;
  left: 0;
  box-sizing: border-box;
  border: solid var(--fourth-color) 2px;
  margin-left: 0.5%;
  overflow: hidden;
display: none;
}

#mobile-menu-close{
  width: 100%;
  font-size: 30px;
  color: white;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  padding-right: 3%;
  box-sizing: border-box;
  cursor: pointer;
  height: 7vh;
  margin-bottom: 0;

}

#mobile-menu-links{

  margin-top: 0;
  list-style: none;
  width: 100%;
  height: 63%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  padding-left: 0;
  margin-bottom: 0;
}

.mobile-menu-links-li {
  border-bottom: solid var(--fourth-color) 1.5px;
  width: 100%;
  cursor: pointer;
  height: 12.5%;
}

.mobile-menu-links-li-a{
  text-decoration: none;
  color: var(--fourth-color);
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}

#mobile-menu-links-selected{
  background-color: var(--fourth-color);
}
#mobile-menu-links-selected a {
  color: var(--third-color);
}

#mobile-menu-info{
  margin-top: 0;
  background-color: black;
  width: 100%;
  height: 30%;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
}

.mobile-menu-info{
  width: 100%;
  height: 22%;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  box-sizing: border-box;
}

.mobile-menu-info-div1{
  height: 100%;
  min-width: 30%;

  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding-left: 2%;
  box-sizing: border-box;
}

.mobile-menu-info-div1 h4 {
  color: white;
  margin-left: 4%;
}

.mobile-menu-info-anchor{

  height: 100%;
  width: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding-left: 1%;
  overflow-x: scroll;
  text-decoration: none;
  color: white;

}

#mobile-menu-info-social{
  border: solid white 2px;
  width: 100%;
  height: 30%;
  box-sizing: border-box;
  margin-top: 0;
  display: flex ;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding-left: 0;
  list-style: none;
}

.mobile-menu-info-social-li{
  height: 100%;
  color: var(--secondary-color);
  margin-left: 1%;
}


.mobile-menu-info-social-li2{
  height: 100%;
  margin-left: 3%;
}

.mobile-menu-info-social-li2-a{
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.mobile-menu-info-social-li2-img{
  height:  20px;
  width: 20px;
}

/* MAIN CONTENT  */

#resident-involvement-main{
  display: none;
}

#resident-involvement-main-mobile {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    box-sizing: border-box;
    background-color: var(--fourth-color);
}

/* H1 */
#resident-involvement-main-h1-mobile {
    text-align: center;
    color: var(--fourth-color);
    background-color: var(--third-color);
    width: 75%;
    border-radius: 8px;
    padding: 2%;
}

/* SECTION 1 */
#resident-involvement-main-section1-mobile {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
}

.resident-involvement-article-mobile {
    border: solid var(--third-color) 2px;
    width: 95%;
    border-radius: 8px;
    background-color: white;
    margin-bottom: 3%;
}

.resident-involvement-article-h2-mobile {
    margin-top: 2%;
    margin-bottom: 1%;
    color: var(--fourth-color);
    background-color: var(--third-color);
    padding: 2%;
}

.resident-involvement-article-p-mobile {
    font-size: 16px;
    line-height: 1.6;
    color: var(--third-color);
    padding: 2%;
}

/* PHOTO ALBUMS */

#resident-involvement-albums-mobile{
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
    background-color: var(--main-color);
  color: var(--fourth-color);
  padding-top: 3%;
}

.resident-album-mobile {
  width: 95%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  margin-bottom: 3%;
  align-items: center;

  border-radius: 5px;
  padding: 1%;
  background-color: var(--third-color);
}

.resident-involvement-album-title-mobile{
  
  padding: 1%;
  background-color: var(--fourth-color);
  color: var(--third-color);
  margin-top: 0;
  max-width: 75%;
  overflow-x: scroll;
  white-space: nowrap;
}

.resident-involvement-album-div{

  width: 100%;
  position: relative;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.4);
}

.resident-album-arrow {
  position: absolute;
  font-size: 2rem;
  color: var(--third-color);
  background-color: var(--fourth-color);
  padding: 0.2rem 0.5rem;
  cursor: pointer;
  z-index: 1;
  transition: 0.2s;
  user-select: none;
  display: flex;
  margin-top: 3%;
}

.resident-album-arrow:hover {
  background-color: white
}

.resident-album-arrow-left {
  left: 0;
}

.resident-album-arrow-right {
  right: 0;
}


.resident-album-viewport {

 width: 75%;
 margin-left: 12.5%;
 height: 100%;
 display: flex;
 flex-direction: row;
 justify-content: space-around;
 align-items: flex-start;
}


/* images */
.resident-album-img {
  width: 40%;
  height: 170px;
  object-fit: cover;
  border-right: solid var(--main-color) 3px;
  box-sizing: border-box;
  cursor: pointer;
  margin-left: 2%;
}

#resident-involvement-main-h5-mobile{
    background-color: var(--third-color);
    color: var(--fourth-color);
    width: 100%;
    padding: 3%;
    box-sizing: border-box;
    margin-bottom: 0;
    font-size: 16px;
    
}

  .resident-involvement-main-h5-a-mobile {
    color: var(--third-color);
    background-color: var(--fourth-color);
    text-decoration: none;
    padding: 0.5%;
    font-weight: 600;
    border-radius: 4px;
    margin-right: 1%;
  }

#image_modal_mobile{
  position: fixed;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.98);
  height: 100%;
  width: 100%;
  z-index: 4;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  padding: 1%;
  box-sizing: border-box;
  display: none;
}

#image_close_div_mobile{

  width: 100%;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  box-sizing: border-box;
  padding-right: 2%;
}

#image_close_span_mobile{
  color: white;
  font-size: 40px;
  cursor: pointer;
}

#image_div_mobile{
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  box-sizing: border-box;

}

#image_div_img_mobile {
  width: 450px;
  height: 450px;
  border: solid var(--fourth-color) 2px;
  object-fit: contain;
  object-position: center;
}


/*   FOOTER */
#index-footer{
  display: none;
}
#index-footer-mobile {
  width: 100%;
  background-color: black;
  color: white;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;
  margin-bottom: 0px;
    padding: 2%;
}

#index-footer-mobile-div1 {
  text-align: center;

}

#index-footer-mobile-p1 {
  font-size: 13px;
  line-height: 1.4;
  margin: 0;
  
}


#index-footer-mobile-div2 {
  display: flex;
  width: 100%;
  justify-content: center;
  align-items: center;

}

#index-footer-mobile-p2 {
  font-size: 13px;
  margin: 0;
}

#index-footer-mobile-a1 {
  font-size: 13px;
  color: white;
  text-decoration: underline;
  text-decoration-color: white;
  padding: 6px 10px;
  border-radius: 6px;
  transition: 0.3s;
}

#index-footer-mobile-a1:hover {
  background-color: var(--third-color);
  color: var(--fourth-color);
}

/* ADMIN */
#index-footer-mobile-div3 {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;

}

#index-footer-mobile-p2{
  display: flex;
  justify-content: space-around;
  align-items: center;
  text-decoration: none;
  color: var(--fourth-color);
}
#index-footer-mobile-p2:hover{
  color: white;
}

